<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow-sm p-3 rounded-lg">
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="row">
            <div class="col-6">
                <div class="sm-form ">
                    <label for="nama">nama</label>
                    <input type="text" id="nama" name="nama" class="form-control form-control-sm" placeholder="nama" v-model="vdata.nama" >
                </div>
                <div class="sm-form ">
                    <label for="title">title</label>
                    <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
                </div>
                <div class="sm-form ">
                    <label for="wilayah">wilayah</label>
                    <input type="text" id="wilayah" name="wilayah" class="form-control form-control-sm" placeholder="Kandangan - Banjarbaru / Programmer" v-model="vdata.wilayah" >
                </div>
                <div class="sm-form ">
                    <label for="nohp">nohp</label>
                    <input type="text" id="nohp" name="nohp" class="form-control form-control-sm" placeholder="62822 ..." v-model="vdata.nohp" >
                </div>
                <div class="sm-form ">
                    <label for="email">email</label>
                    <input type="email" id="email" name="email" class="form-control form-control-sm" placeholder="email" v-model="vdata.email" >
                </div>
                  <div class="sm-form ">
                    <label for="gambar">gambar</label>
                    <input type="file" id="filenya" name="gambar" class="form-control form-control-sm" placeholder="gambar" @change="upload" >
                    <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                </div>
            </div>
            <div class="col-6">
                  <div class="sm-form ">
                      <label for="facebook">facebook</label>
                      <input type="text" id="facebook" name="facebook" class="form-control form-control-sm" placeholder="facebook" v-model="vdata.facebook" >
                  </div>
                  <div class="sm-form ">
                      <label for="instagram">instagram</label>
                      <input type="text" id="instagram" name="instagram" class="form-control form-control-sm" placeholder="instagram" v-model="vdata.instagram" >
                  </div>
                  <div class="sm-form ">
                      <label for="linkedin">linkedin</label>
                      <input type="text" id="linkedin" name="linkedin" class="form-control form-control-sm" placeholder="linkedin" v-model="vdata.linkedin" >
                  </div>
                  <div class="sm-form ">
                      <label for="github">github</label>
                      <input type="text" id="github" name="github" class="form-control form-control-sm" placeholder="github" v-model="vdata.github" >
                  </div>
                  <div class="sm-form ">
                      <label for="cv">Link cv</label>
                      <input type="text" id="cv" name="cv" class="form-control form-control-sm" placeholder="cv" v-model="vdata.cv" >
                  </div>
                  <div class="sm-form ">
                      <label for="rekening">rekening</label>
                      <input type="text" id="rekening" name="rekening" class="form-control form-control-sm" placeholder="rekening" v-model="vdata.rekening" >
                  </div>
            </div>
          </div>
          

           <p class="p-3 pl-0">
            Alamat
          </p>
          <wysiwyg v-model='vdata.alamat' :options="{image: {  uploadURL: 'https://gis.officialapp.website/upload1.php',  dropzoneOptions: {}}}"/>

          <p class="p-3 pl-0">
            Tentangku
          </p>
          <wysiwyg v-model='vdata.tentang' :options="{image: {  uploadURL: 'https://gis.officialapp.website/upload1.php',  dropzoneOptions: {}}}"/>
        
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow-sm rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['tentang','alamat']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
let urlnya ='https://gis.officialapp.website'
export default {
    layout:'app_admin',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    upload(){
      let file = document.querySelector("#filenya"); // berikan id pada input file
      sdb.collection('tbuser',false,urlnya).upload(file,true,true) // file,compress,scramble
      .then(res=>{
        this.vdata.gambar=res;
        this.$forceUpdate();
      })
    },
    getData(){
      sdb.collection("app_portofolio_about",false,urlnya).doc().select(`select * from app_portofolio_about where iduser='${this.$store.state.users.id}'`).then(res => {
        if(res.length>0){
          this.vdata=res[0]
          this.btn='update'
        }
        this.datanya=res;
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      that.vdata.iduser=this.$store.state.users.id
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_portofolio_about",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_portofolio_about",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_portofolio_about",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>